<template>
	<div class="foodDetail" v-show='detailShow'>
		<div class="food-img">
			<img :src="food.image" alt="">
		</div>
		<cartControl :food ='food'></cartControl>
	</div>
</template>
<script>
	
	import { mapGetters } from 'vuex'
	import cartControl from '../cartControl/cartControl.vue'

	export default {

		computed : {
			...mapGetters([
				'detailShow',
				'food'
			])
		},
		components:{
			cartControl
		}
	}

</script>
<style lang='less' scoped>
	.foodDetail{
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		// z-index: 9999999999;
		left: 0;
		background: pink;
		.food-img{
			position: relative;
			width: 100%;
			height: 0;
			padding-top: 100%;
			img{
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
			}
		}
	}
</style>












