import axios from 'axios';
import TYPES from '../types.js'

const state = {
	ccc : 666,
	goods : [],
	detailShow : false,
	food : {}
}

const getters = {
	ccc(state){
		return state.ccc;
	},
	goods(state){
		return state.goods;
	},
	detailShow( state ){
		return state.detailShow;
	},
	food( state ){
		return state.food;
	}
}

const actions = {
	getGoods( {commit} ){
		axios.get('./data.json').then( res => {
			commit( TYPES.GET_GOODS , res.data.goods );
		})
	},
	showFoodDetail( { commit } , food ){
		commit( TYPES.SHOW_FOODDETAIL ,food );
	}
}

const mutations = {
	[TYPES.GET_GOODS](state,data){
		// console.log(data);
		state.goods = data;
	},
	[TYPES.SHOW_FOODDETAIL](state,food){
		state.detailShow = true;
		state.food = food;
	}

}

export default {
	state,
	getters,
	mutations,
	actions
}


