<template>
	<div class="goods">
		<div class="menu-wrap" ref='menuwrap'>
			<!-- {{currentIndex}} -->
			<ul>
				<li @click='selectMenu(i)' :class='{"current" : currentIndex == i }' class="menu-item" v-for='(val,i) in goods'>
					<span class="text">
						{{val.name}}{{i}}
					</span>
				</li>
			</ul>
		</div>
		<div class="foods-wrap" ref='foodwrap'>
			
			<ul>
				<li v-for='item in goods' class="food-list food-list-hook" >
					<h1 class="title">{{ item.name }}</h1>
					<ul>
						<li v-for = 'food in item.foods' class="food-item" @click='showFoodDetail(food)'>
							{{ food.name }}
							<div class="ccc">
								<cartControl :food = 'food'></cartControl>
							</div>
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<foodDetail></foodDetail>
		<cart></cart>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'
	import BScroll from 'better-scroll'
	import foodDetail from '../foodDetail/foodDetail.vue'
	import cart from '../cart/cart.vue'
	import cartControl from '../cartControl/cartControl.vue'

	export default{
		components:{
			foodDetail,
			cart,
			cartControl
		},
		computed:{
			...mapGetters([
				'getters',
				'goods'
			]),
			// 计算当前位置在哪一区间内 然后返回当前i
			currentIndex(){
				for( let i = 0 ; i < this.heights.length ; i++){
					let height1	= this.heights[i];
					let height2	= this.heights[i+1];
					if( this.scrollY >= height1 && this.scrollY < height2){
						return i;
					}
				}
				return 0;
			}
		},
		data(){
			return {
				cc : 1469281964000,
				heights:[],
				scrollY : ''
			}
		},
		mounted(){
			this.$nextTick( () => {
				this.initBScroll();
				this.calcHeight();
			})
		},
		methods:{
			initBScroll(){
				// 右侧滚动
				if( !this.foodwrapBScroll ){
					this.foodwrapBScroll = new BScroll( this.$refs.foodwrap,{
						click : true,
						probeType : 3
					})
				}else{
					this.foodwrapBScroll.refresh();
				};
				this.foodwrapBScroll.on('scroll', (pos) => {
					// console.log(pos);
					this.scrollY = Math.abs( Math.round( pos.y))
					// console.log(this.scrollY);
				}) 

				// 左侧滚动
				if( !this.menuwrapBScroll ){
					this.menuwrapBScroll = new BScroll( this.$refs.menuwrap,{
						click : true
					})
				}else{
					this.menuwrapBScroll.refresh();
				};
			},
			calcHeight(){
				let foodList = document.getElementsByClassName('food-list-hook');
				let height = 0 ;
				this.heights.push( height );
				for(let i = 0 ; i < foodList.length ; i++){
					//获取每一个li（区块）的高度
					height += foodList[i].clientHeight;
					this.heights.push( height );
				}
				// [0, 599, 684, 769, 968, 1110, 1252, 1451, 1765, 2136]
				// console.log(this.heights);

			},
			// 左侧点击事件
			selectMenu(i){
				// console.log(i)
				let foodList = document.getElementsByClassName('food-list-hook');
				this.foodwrapBScroll.scrollToElement(foodList[i],300);
			},
			showFoodDetail( f ){
				this.$store.dispatch('showFoodDetail',f);
			}
		}

	}
</script>

<style lang='less' scoped>
	.goods{
		position: absolute;
		display: flex;
		overflow: hidden;
		width: 100%;
		top:3.5rem;
		bottom: 1rem;
		.menu-wrap{
			/* 不扩展 不缩放 */
			flex: 0 0 1.6rem;
			width: 1.6rem;
			.menu-item{
				width: 1.12rem;
				height: 1.08rem;
				font-size: 0.24rem;
				background: #f3f5f7;
				color:rgb(7,17,27);
				padding: 0 0.24rem;
				font-weight: 200;
				line-height: 0.28rem;
				display: table;
				&.current{
					background: pink;
				}
				.text{
					width: 1.12rem;
					display: table-cell;
					vertical-align: middle;
					border-bottom: 1px solid rgba(7,17,27,0.1);
				}
			}

		}
		.foods-wrap{
			flex:1;
			.food-list{
				.title{
					width: 100%;
					height: 0.5rem;
					line-height: 0.2rem;
					font-size: 0.24rem;
					color:red;
					background: rgba(7,17,27,0.1)
				}
				ul{
					.food-item{
						width: 100%;
						height: 1rem;
						border: 1px solid #000;						
						position: relative;
						line-height: 1rem;
						.ccc{
							width: 1.5rem;
							position: absolute;
							right: 0.2rem;
							top:0.2rem;
							height: 0.5rem;
						}
					}
				}
			}
		}
	}
</style>


















