<template>
	<div class="cartControl">
		<div class="reduce" @click.stop='reduce' v-show="food.count>0">-</div>
		<div class="count" v-show="food.count>0">{{food.count}}</div>
		<div class="add" @click.stop='add'>+</div>
	</div> 
</template>

<script>
	import Vue from 'vue'

	export default{

		props:['food'],
		methods:{
			add(){
				if( !this.food.count ){
					Vue.set(this.food , 'count' , 1);
				}else{
					this.food.count ++;
				}
			},
			reduce(){
				if( this.food.count ){
					this.food.count --;
				}
			}
		}
	}
</script>

<style lang='less' scoped>
	
	.cartControl{
		width: 1.5rem;
		height: 0.5rem;
		display: flex;
		
		div{
			flex:1;
			width: 0.5rem;
			height: 0.5rem;
			border: 1px solid red;
			line-height: 0.5rem;
			text-align: center;
		}
	}
</style>


