<template>
	<div class="ratingSelect">
		<div class="type">
			<div @click='select(2)' class="one" :class="{'active' : selectType === 2 }">{{types[2]}}{{ratings.length}}</div>
			<div @click='select(0)' class="two" :class="{'active' : selectType === 0 }">{{types[0]}}{{positiveCount}}</div>
			<div @click='select(1)' class="three" :class="{'active' : selectType === 1 }">{{types[1]}}{{negativeCount}}</div>
		</div>
		<div class="only" :class='{"on" : onlyContent}' @click='toggle'>
			看内容
		</div>
	</div>

</template>

<script>
	
	import { mapGetters } from 'vuex'

	export default{
		computed:{
			...mapGetters([
				'types',
				'selectType',
				'onlyContent',
				'ratings'
			]),
			positiveCount(){
				return this.ratings.filter( rating => {
					return rating.rateType === 0;
				}).length;
			},
			negativeCount(){
				return this.ratings.filter( rating => {
					return rating.rateType === 1;
				}).length;
			}
		},
		methods:{
			select( type ){
				this.$store.dispatch('select',{type});
			},
			toggle(){
				this.$store.dispatch('toggle');
			}
		},
		mounted(){
			this.$store.dispatch('changTypes',['推荐','吐槽','全部']);
		}

	}
</script>

<style lang='less' scoped>
	.ratingSelect{
		.type{
			width: 100%;
			height: 1rem;
			display: flex;
			text-align: center;
			line-height: 1rem;			
			.one{
				flex:1;
				border: 1px solid #000;
				&.active{
					color:red;
				}
			}
			.two{
				flex:1;
				border: 1px solid #000;
				&.active{
					color:red;
				}
			}
			.three{
				flex:1;
				border: 1px solid #000;
				&.active{
					color:red;
				}
			}
		}
		.only{
			width: 1rem;
			height: 1rem;
			border: 1px solid #000;
			text-align: center;
			margin-top: 0.2rem;
			line-height: 1rem;
			&.on{
				background: pink;
			}
		}
	}
</style>


