<template>
	<div class="ratings">
		ratings
		<br>
		<Star :score = '2.6'></Star>
		<Star :score = '3.6' :size='"small"'></Star>
		<br>
		<div class="rating">
			<h1 class="cc">商品评价</h1>
			<rating-select></rating-select>
			<div class="rating-wrap" ref='ratingWrap'>
				<ul>
					<li v-for = 'rating in selectRatings' class="rating-item">
						<div class="time">
							{{rating.username}}
						</div>
						<div class="text">
							{{rating.text}}
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	
	import Star from '../star/star.vue'
	import { mapGetters } from 'vuex'
	import BScroll from 'better-scroll'
	import ratingSelect from '../ratingSelect/ratingSelect.vue'

	export default{
		
		computed : {
			...mapGetters([
				'seller',
				'ratings',
				'selectRatings'
			])
		},
		watch:{
			selectRatings(){
				if( !this.ratingBScroll ){
					this.ratingBScroll = new BScroll( this.$refs.ratingWrap ,{
						click : true
					})
				}else{
					this.ratingBScroll.refresh();
				}
			}
		},
		components : {
			Star,
			ratingSelect
		}

	}
</script>

<style lang='less' scoped>
	.ratings{
		width: 100%;
		top: 3.52rem;
		bottom: 0;
		.rating{
			
			padding: 0.3rem;
			border-bottom: 1px solid red;
			.cc{
				width: 100%;
				color: red;
				height: 1rem;
				line-height: 1rem;
				background: pink;
			}
			.rating-wrap{
				width: 100%;
				height: 7.6rem;
				overflow: hidden;
				.rating-item{
					width: 100%;
					height: 1rem;
					border-bottom: 1px dashed red;
					line-height: 0.4rem;
				}
			}
		}
	}
</style>


