<template>
	<div class="cart">
		<div class="one" :class='{"heightlight" : totalCount > 0}'>{{totalPrice}}元</div>
		<div class="one">{{totalCount}}个</div>
		<div class="one">配送：4元</div>
		<div class="one">{{payDesc}}</div>
	</div>
</template>

<script>
	
	import { mapGetters } from 'vuex'

	export default {

		computed:{
			...mapGetters([
				'goods',
				'seller'
			]),
			selectFoods(){
				let foods = [];
				// 遍历大的分类
				this.goods.forEach( good => {
					// 遍历分类里面的每一个商品
					good.foods.forEach( food => {
						// 如果商品的count大于0  就放进定义的数组中去
						if( food.count > 0 ){
							foods.push(food);
						}
					})
				});
				// dom是数据的自然映射，数据改变则dom随之更新
				return foods;
			},
			totalPrice(){
				let total = 0;
				// 对上面的计算属性进行遍历（就是对数组遍历，里面的每一个对象都有count）
				this.selectFoods.forEach( food => {
					total += food.count * food.price;
				});
				return total;
			},
			totalCount(){
				let count = 0 ;
				this.selectFoods.forEach( food => {
					count += food.count;
				});
				return count;
			},
			payDesc(){
				if( this.totalPrice === 0){
					return `￥${this.seller.minPrice}元起送`;
				}else if( this.totalPrice < this.seller.minPrice ){
					return `还差￥${this.seller.minPrice - this.totalPrice}元起送`
				}else{
					return "去结算";
				}
			}
		}

	}
</script>

<style lang='less' scoped>
	.cart{
		width: 100%;
		height: 1rem;
		position: fixed;
		bottom: 0;
		left: 0;
		background: pink;
		display:flex;
		.one{
			flex:1;
			box-sizing: border-box;
			border: 1px solid #000;
			text-align: center;
			line-height: 1rem;
			&.heightlight{
				color:blue;
			}
		}
	}
</style>


