<template>
	<div class="header">
		<div class="bg">
			<img :src="seller.avatar" alt="">
		</div>
		<div class="avatar" @click='showDetail'>
			<img :src="seller.avatar" alt="">			
		</div>
		<div class="title">
			<p>{{seller.name}}</p>
			<p v-if='seller.supports'>{{seller.supports[0].description}}</p>
			<p v-if='seller.supports'>{{seller.supports.length}}</p>
		</div>
		<sellerDetail></sellerDetail>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'
	import sellerDetail from '../sellerDetail/sellerDetail.vue'

	export default{

		computed : {
			...mapGetters([
				'seller'
			])
		},
		components:{
			sellerDetail
		},
		methods:{
			showDetail(){
				this.$store.dispatch('showDetail');
			}
		}
	}
</script>
	
<style lang="less" scoped>
	.header{
		width: 100%;
		height: 2.68rem;
		background:rgba(7,17,27,0.5);
		padding: 0.48rem 0.24rem 0 0.48rem;
		box-sizing: border-box;
		position: relative;
		.avatar{
			width: 1.28rem;
			height: 1.28rem;
			display: inline-block;
		}
		.title{
			width: 3rem;
			height: 1.28rem;
			display: inline-block;
			vertical-align: top;
			p{	
				margin-top: 0.2rem;
			}
		}
		.bg{
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			z-index: -5;
			filter:blur(5px);
			img{
				width: 100%;
				height: 100%;
			}
		}
		img{
			width: 1.28rem;
			height: 1.28rem;
		}
	}
</style>


