<template>
	<div class="cc" id="cc" ref = 'ccc'>
		<div>
			{{seller.name}}
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<div class="pics">
				<h1 class="title">商家实景</h1>
				<div class="pic-wrapper" ref='picwrap'>
					<ul class="pic-list" ref = 'piclist'>
						<li class="pic-item" v-for='pic in seller.pics'>
							<img :src="pic" width="120" height="90">
						</li>
					</ul>
				</div>
			</div>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
			<p>hello world nice to meet you</p>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'
	import BScroll from 'better-scroll'

	export default{
		computed : {
			...mapGetters([
				'seller'
			])
		},
		watch:{
			seller(){
				this._initPicture();
			}
		},
		mounted(){
			this.$nextTick( () => {
				if( !this.sellerSCroll ){
					this.sellerSCroll = new BScroll( this.$refs.ccc ,{
					});
				}else{
					this.sellerSCroll.refresh();
				}
			});
			this._initPicture();
		},
		methods:{
			_initPicture(){
				if( this.seller.pics){
					let pw = 120;
					let m = 12;
					let w = ( pw + m ) * this.seller.pics.length - m;
					this.$refs.piclist.style.width = w + 'px';

					if( !this.picBScroll ){
						this.picBScroll = new BScroll( this.$refs.picwrap ,{
							scrollX : true ,
							eventPassthrough : 'vertical'
						})
					}else{
						this.picBScroll.refresh();
					}
				}

			}
		}
	}
</script>

<style lang='less' scoped>
	.cc{
		position: absolute;
		top: 3.6rem;
		bottom: 1rem;
		z-index: -55;
		.pics{
			width: 100%;
			padding: 0.36rem 0 0.36rem 0.36rem;
			.title{
				padding-bottom: 0.24rem;
				font-size: 0.36rem;
			}
			.pic-wrapper{
				width: 6.6rem;
				white-space: nowrap;
				overflow: hidden;
				.pic-item{
					display: inline-block;
					margin-right: 0.12rem;
					&:last-child{
						margin: 0;
					}
				}
			}
		}
		p{
			font-size: 30px;
		}
	}


















</style>


