<template>
    <div id="app">
        <v-header></v-header>

        <!-- 路由切换 -->
        <div class="tab">
            <div class="tab-item">
                <router-link to='/goods'>商品</router-link>    
            </div>
            <div class="tab-item">
                <router-link to='/ratings'>评价</router-link>    
            </div>
            <div class="tab-item">
                <router-link to='/seller'>商家</router-link>    
            </div>
        </div>

        <router-view/>
    </div>
</template>

<script>
    import header from './components/header/header.vue'

    export default {
        components:{
            'v-header' : header
        },
        mounted(){
            this.$store.dispatch('getSeller');
            this.$store.dispatch('getGoods');
            this.$store.dispatch('getRatings');
        }
    }
</script>

<style lang='less' scoped>
    #app{
        .tab{
            background: #fff;
            width: 100%;
            height: 0.8rem;
            font-size: 0.28rem;
            color:rgb(240,20,20);
            line-height: 0.8rem;
            display: flex;
            border-bottom:1px solid rgba(7,17,27,0.1);
            .tab-item{
                flex:1;
                text-align: center;
                a{
                    display: block;
                    &.active{
                        color:red;
                        background: pink;
                    }
                }
            }
        }
    }
</style>


























